<template>
  <v-container fluid>
    <!-- <v-row align="center">
      <v-col cols="auto">
        <h1 class="page-title">添加新商品</h1>
      </v-col>
      <v-spacer />

      <v-col cols="auto">
        <v-btn color="success" @click="save">Save</v-btn>
      </v-col>
    </v-row> -->

    <v-row>
      <v-col md="10" sm="12">
        <v-card>
          <v-card-title> 商品信息 </v-card-title>
          <v-card-text>
            <v-row>
              <v-col cols="10">
                <v-text-field
                  prepend-inner-icon="mdi-format-title"
                  label="Title"
                  v-model="title"
                ></v-text-field>
              </v-col>
              <v-col cols="2">
                <v-text-field
                  prefix="$"
                  label="Price"
                  v-model="price"
                  :rules="[rules.number]"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12">
                <v-textarea
                  rows="8"
                  label="Description"
                  v-model="description"
                  counter
                  outlined
                  clearable
                ></v-textarea>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>

      <v-col md="4" sm="12">
        <v-card>
          <v-card-title> image </v-card-title>
          <v-card-text>
            <v-file-input
              label="File Input"
              accept="image/png, image/jpeg, image/bmp"
              show-size
              counter
              prepend-icon="mdi-camera"
              laceholder="Select your image"
              v-model="image"
            ></v-file-input>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-overlay :value="overlay">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
  </v-container>
</template>

<script>
import product from "@/api/service/product.js"; // 导入商品模块
export default {
  name: "ProductCreate",
  data: () => ({
    title: "",
    price: "0.0",
    image: [],
    description: "",
    overlay: false,
    rules: {
      number: (value) => {
        // 只允许 1-2位小数的正实数
        const pattern = /^[0-9]+(.[0-9]{1,2})?$/;
        return pattern.test(value) || "Invalid Number";
      },
    },
  }),

  methods: {
    save() {
      if (this.title && this.image) {
        return new Promise((resolve, reject) => {
          this.overlay = true; // 打开带加载器的遮罩层
          product
            .create(
              this.title,
              Number(this.price),   // 把价格转为数字类型
              this.image,
              this.description
            )
            .then((resp) => {
              console.log(resp);
              this.overlay = false; // 关闭遮罩层
              resolve(resp);
            })
            .catch((error) => {
              console.log(error);
              this.overlay = false; // 关闭遮罩层
              reject(resp);
            });
        });
      } else {
        console.log("Title or Image in empty.");
      }
    },
  },
};
</script>

