import LC from 'leancloud-storage';

/**
 * @description: 获取所有商品
 * @param {*} skip : （页码-1）*每页大小
 * @param {*} limit ： 每页大小
 * @return {*}
 */
let getProducts = (skip = 0, limit = 10) => {
    return new Promise((resolve, reject) => {
        let query = new LC.Query('Product');   // 创建一个查询对象
        query.include('owner', 'image')        // 用include 包含其他关联的Class
            .descending('createdAt')
            .skip(skip)                        // 跳过前多少条记录
            .limit(limit)                      // 每次查询获取的记录数
            .find()                            // 不加参数的 find() 表示查询 所有记录
            .then(products => {
                console.log(products);
                resolve(products);             // 返回查询成功后的结果 
            })
            .catch(error => {
                console.log(error);
                reject(error);                 // 返回错误信息
            });
    });
};

let getProductCount = () => {
    return new Promise((resolve, reject) => {
        let query = new LC.Query('Product');   // 创建一个查询对象
        query.count()
            .then(count => {
                console.log("Total Count:", count);
                resolve(count);
            })
            .catch(error => {
                console.log(error);
                reject(error);
            });
    });
};

let create = (title, price = 0.0, image = '', description = '') => {
    return new Promise((resolve, reject) => {
        // 创建 Product 对象，如果leancloud项目中没有对应的表，则自动创建
        let Product = LC.Object.extend('Product');
        let product = new Product();   // 实例化 Product 对象
        let currentUser = LC.User.current();   // 获取当前用户
        let imageName = image.name;            // 获取图片的文件名
        let imageFile = new LC.File(imageName, image);   // 上传文件

        // 设置各字段的值
        product.set('title', title);
        product.set('price', price);
        product.set('description', description);
        product.set('owner', currentUser);
        product.set('image', imageFile);

        //保存数据到服务端
        product.save().then(prod => {
            console.log('已成功发布', prod);
            resolve(prod);   // 返回操作成功后的结果
        }, error => {
            console.log('发布失败', error);
            reject(error);   // 返回操作失败的错误信息
        }).catch(error => {
            console.log('发布失败', error);
            reject(error);   // 返回操作失败的错误信息
        });
    });

};

let deleteProductById=(id)=>{
    let prod = LC.Object.createWithoutData('Product', id);
    return new Promise((resolve, reject)=>{
        prod.destroy()
        .then(resp=>{
            console.log('deleteProductById: '. resp);
            resolve(resp);
        })
        .catch(error=>{
            console.log("deleteProductById: ",error);
            reject(error);
        });
    });
};

let getProductById =(id)=>{
    let query = new LC.Query('Product');   // 创建一个查询对象
    return new Promise((resolve, reject)=>{
        query.include('owner','image')
        .get(id)                           // 查询指定id的记录
        .then(prod=>{
            console.log('getProductById',prod)
            resolve(prod);                 // 返回查询成功的结果
        })
        .catch(error=>{
            console.log('getProductById'.error)
            resolve(error);
        });
    })
};

// 当前模块导出的方法
export default {
    getProducts,
    create,
    getProductCount,
    deleteProductById,
    getProductById,
}